import { defineStore } from 'pinia'

export default defineStore('statusStore', {
  state: () => ({
    isLoading: [],
    cartLoadingItem: '',
    messages: []
  }),
  actions: {
    pushManager (res = true, title = '', content = '') {
      if (res) {
        const obj = {
          style: 'success',
          title: `${title}`,
          content: `${content}`
        }
        this.messages.push(obj)
      } else {
        const obj = {
          style: 'danger',
          title: `${title}`,
          content: `${content}`
        }
        this.messages.push(obj)
      }
    }
  }
})
